.wea-input-number {
  display: inline-block;
  position: relative;
  width: 100%;
  word-break: keep-all;
}
.wea-input-number .ant-input-number {
  width: 100%;
}
.wea-input-number .input-tip {
  position: absolute;
  top: 6px;
  left: 100%;
}
.wea-input-number .has-input {
  border-radius: 0;
  border: 1px solid #e1e1e1;
}
.wea-input-number .has-input:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
}
.wea-input-number .right {
  text-align: right;
}



