.wea-helpful-tip .icon-coms-Explain {
  font-size: 16px;
  vertical-align: text-bottom;
  color: #FAA825;
}
.wea-helpful-tip .icon-coms-Explain:before {
  color: #FAA825;
}
.wea-helpful-tip-wrapper {
  word-break: break-all;
  opacity: 1;
}
.wea-helpful-tip-wrapper .ant-tooltip-arrow {
  /*width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 5px 5px 5px;*/
  border-color: transparent transparent #B5AC85 transparent;
}
.wea-helpful-tip-wrapper .ant-tooltip-arrow::before {
  position: absolute;
  content: '';
  top: 1px;
  left: -4px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 4px 5px 4px;
  border-color: transparent transparent #FFF8C3 transparent;
}
.wea-helpful-tip-wrapper .ant-tooltip-inner {
  max-width: 100%;
  min-height: 0;
  color: #666666;
  line-height: 18px;
  padding: 4px 5px;
  background: #FFF8C3;
  border: 1px solid #B5AC85;
  border-radius: 3px;
  -webkit-box-shadow: none;
          box-shadow: none;
}
.wea-helpful-tip-wrapper-center .ant-tooltip-inner {
  text-align: center;
}



